.about-me {
    padding: 10px;

}

.top {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-pack: center;
        justify-content: center;
    -ms-flex-align: center;
        align-items: center;
}

.resume-info {
    font-size: 1rem;
    display: grid;
    grid-template-columns: 50% 50%;
    grid-template-rows: auto;
}
.resume-info > div {
    padding: 10px 50px 10px 12rem
}

.resume-info .value {
    font-size: 1.125rem
}



